'use strict';

// write code here

function convertNumber(number) {
  const final = number;

  const final2 = final.slice(1).split(',').join('');

  return +final2;
}

const listLi = document.querySelectorAll('li');

function getEmployees(list) {
  const copy4 = [];

  for (let i = 0; i < list.length; i++) {
    const copy3 = {};

    copy3.person = list[i].innerText;
    copy3.salary = convertNumber(list[i].dataset.salary);
    copy4.push(copy3);
  }

  return copy4;
}

function sortList(list2) {
  const listDom = document.querySelectorAll('li');

  for (let i = 0; i < list2.length; i++) {
    list2.sort((a, b) => b.salary - a.salary);
    listDom[i].innerText = list2[i].person;
  }
}

sortList(getEmployees(listLi));
